.wordpress-cards-left-main-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-height: fit-content;
  background-color: #e5e5e5;
  flex-wrap: wrap;
}

.wordpress-cards-left-main-container-box-1 {
  flex: 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.wordpress-cards-left-main-container-box-1:hover {
  scale: 1.1;
  transition: all 0.2s ease-out;
}

.wordpress-cards-left-main-container-box-1a {
  min-height: fit-content;
  width: 80%;
  background-color: #612d91;
  display: flex;
  flex-direction: column;
  /* justify-content: space-around */
  /* align-items: center; */
  gap: 20px;
  padding: 7% 7%;
  border-radius: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  opacity: 0;
}

.wordpress-cards-left-main-container-box-1a h3 {
  font-size: 1.3rem;
  line-height: 1.75;
  color: white;
  text-align: left;
}

.wordpress-cards-left-main-container-box-1a p {
  font-size: 1rem;
  line-height: 1.75;
  color: white;
  text-align: left;
}

.wordpress-cards-left-main-container-box-1a button {
  background-color: #fff;
  color: #612d91;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  padding: 2% 5%;
  /* width: 50%; */
  border: none;
  align-self: flex-start;
}

.wordpress-cards-left-main-container-box-2 {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wordpress-cards-left-main-container-box-2 img {
  width: 75%;
}

.wordpress-cards-left-slide-in-animation {
  position: relative;
  left: -100%;
  animation: wordpress-cards-left-slide-in 2s forwards;
}

@keyframes wordpress-cards-left-slide-in {
  0% {
    position: relative;
    left: -100%;
    opacity: 0;
  }
  100% {
    position: relative;
    left: 0%;
    opacity: 1;
  }
}

@media (max-width: 767px) {
  .wordpress-cards-left-main-container {
    flex-direction: column;
    padding-top: 2.5vh;
  }

  .wordpress-cards-left-main-container-box-1a button {
    align-self: center;
  }
}
