.sub-categories-banner {
  min-height: fit-content;
  height: calc(100vh - 80px);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5vh;
  padding: 20vw 7vw;
}

.sub-categories-banner-buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5%;
  width: 100%;
}

.sub-categories-banner h1 {
  color: #fff;
  font-size: 2.25rem;
  text-align: center;
}

.sub-categories-banner p {
  color: #fff;
  font-size: 1.75rem;
  text-align: center;
  line-height: 1.25;
}

.sub-categories-banner-buttons button {
  padding: 1%;
  font-size: 1rem;
  height: fit-content;
  min-width: fit-content;
  width: 12.5%;
  font-weight: bolder;
  background-color: #fff;
  color: #612d91;
  cursor: pointer;
  border: none;
  cursor: pointer;
  border: none;
  transition: all 0.4s ease-in;
}

.sub-categories-banner-buttons button:hover {
  background-color: #612d91;
  color: #fff;
}

@media (max-width: 767px) {
  .sub-categories-banner {
    height: 30vh;
    padding: 25vh 7vw;
  }

  .sub-categories-banner h1 {
    font-size: 2rem;
  }

  .sub-categories-banner p {
    font-size: 1.25rem;
  }

  .sub-categories-banner-buttons button {
    font-size: 0.8rem;
    padding: 1.75vh 1.75vw;
  }
}
